body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{
  --color-grey-dark:    #404040;
  --color-grey-light:   #aaa;
  --color-blue-light:   #11B3F1;
  --color-blue-dark:    #10A8E3;
  --color-yellow-light: #F1CB3B;
  --color-yellow-dark:  #E3BF38;
  --color-mint-light:   #00DDBF;
  --color-mint-dark:    #00D0B4;
  --color-red-light:    #FF4356;
  --color-red-dark:     #F03F51;
  --color-purple-light: #BD1CF0;
  --color-purple-dakr:  #B21AE2;
  --color-coral-light:  #fa715e;


  --font-primary: 'Open Sans', sans-serif, 'Roboto';
  --font-logo: 'Times New Roman', Times, serif;

}

* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html{
  box-sizing: border-box;
  font-size: 62.5%; /* So that 1rem = 10px (10px/16px (browsers default) = 62.5% -> 1rem = 10px */
  /* These two are used to keep the page in place while scrolling */
  height: 100%;
  overflow: hidden;
}

h2 {
  font-family: 'Open Sans', sans-serif, 'Roboto';
  font-family: var(--font-primary);
  font-weight: 600;
  font-size: 2.3rem;
}

h4 {
  text-transform: uppercase;
  font-weight: 900;
}

a, a:visited, a:hover, a:active {
  color: inherit;
}

.App {
  display: grid;
  grid-template-rows: [header-start] 7.6rem [header-end main-start] calc(100vh - 7.6rem) [main-end];
}

/* Fade animation */
.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}
.fade-exit {
  opacity: 1;

}
.fade-exit-active {
  opacity: 0;
  transition: opacity 500ms ease-in;
}

/* MEDIA QUERIES */
/* ------------- */
/* --- WIDTH --- */
/* -- HEIGHT  -- */
@media only screen and (max-width: 768px),
only screen and (max-height: 480px){
  .App {
    grid-template-rows: [header-start] 5rem [header-end main-start] calc(100vh - 5rem) [main-end];
  }

  h2 {
    font-size: 1.7rem;
  }
  
  h4 {
    font-size: 0.5rem;
  }
}
.header{
    z-index: 1;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    
}

.logo-container{
    -webkit-align-self: flex-start;
            align-self: flex-start;
    /* To center the middle item */
    width: 50%; 
    -webkit-flex-shrink: 1; 
            flex-shrink: 1; 
    display: -webkit-flex; 
    display: flex; 
}

.logo{
    margin-left: 10rem;
    background-color: black;
    width: 12rem;
    height: 12rem;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    cursor: default;
}

.logo__text{
    font-family: 'Times New Roman', Times, serif;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 1.4rem;
    color: white;
    z-index: 2;
}

.center_navigation{
    font-weight: 700;
    font-size: 1.4rem;
    color: var(--color-grey-dark);

    /* Each text item has a padding and a border-bottom. I use margin-bottom: 
        these two properties to center again the whole thing ( with the border) */
    margin-bottom: .6rem;
    -webkit-flex-shrink: 0;
            flex-shrink: 0; 
}

.center_navigation__about,
.center_navigation__work{
    text-decoration: none;
    padding: .3rem;
    border-bottom: 3px solid var(--color-grey-light);
    transition: border-bottom .3s cubic-bezier(0,0,0,.3);
}
.center_navigation__email{
    text-decoration: none;
    padding: .2rem;
    border-bottom: 1.5px solid var(--color-grey-light);
    transition: border-bottom .3s cubic-bezier(0,0,0,.3);
}

.center_navigation__about{
    margin-right: 2rem;
}

.center_navigation__work{
    margin-left: 2rem;
}

.center_navigation__about:hover,
.center_navigation__work:hover,
.headernav-active{
    border-bottom: 3px solid black;
}
.center_navigation__email:hover{
    border-bottom: 1.5px solid black;
}

.headernav-active{
    pointer-events: none;
    cursor: default;
}

.social{
    /* To center the middle item */
    width: 50%; 
    -webkit-flex-shrink: 1; 
            flex-shrink: 1; 
    display: -webkit-flex; 
    display: flex; 
    -webkit-flex-direction: row-reverse; 
            flex-direction: row-reverse;
}
.social__linkedin,
.social__github{
    width: 2.1rem;
    height: auto;
    transition: opacity .2s ease-out;
}

.social__linkedin:hover,
.social__github:hover{
    opacity: 0.4;
}

.social__linkedin{
    margin-right: 5rem;
}

.social__github{
    margin-right: 2rem;
}

