.main{
    position: relative;
    margin-left: 4rem;
    margin-right: 4rem;
}

/* black box that expands when pressing Work on header. At first it's hidden (width: 0px)*/
.main::after{
    content: '';
    position: absolute;
    top: 0;
    right: 0px;
    height: 100%;
    left: 100vw;
    background-color: #000;
    z-index: 1; 
}

.mainGif{     
    position: absolute;
    top: 0;
    left: 0;
    max-width:100%;
    width: auto;
    z-index: 2;
    
    opacity: 0;
}

.mainGif__animation{
    -webkit-animation: gifAnimation .7s cubic-bezier(.36,.03,.98,-0.08) 0s;
            animation: gifAnimation .7s cubic-bezier(.36,.03,.98,-0.08) 0s;
}

.animateMain1-1::after{
    -webkit-animation: main1-1 .7s cubic-bezier(.36,.03,.98,-0.08) 0s;
            animation: main1-1 .7s cubic-bezier(.36,.03,.98,-0.08) 0s;
}

.animateMain1-2::after{
    -webkit-animation: main1-2 .7s cubic-bezier(.36,.03,.98,-0.08) 0s;
            animation: main1-2 .7s cubic-bezier(.36,.03,.98,-0.08) 0s;
}

.animateMain2-1::after{
    -webkit-animation: main2-1 .7s cubic-bezier(.36,.03,.98,-0.08) 0s;
            animation: main2-1 .7s cubic-bezier(.36,.03,.98,-0.08) 0s;
}

.animateMain2-2::after{
    -webkit-animation: main2-2 .7s cubic-bezier(.36,.03,.98,-0.08) 0s;
            animation: main2-2 .7s cubic-bezier(.36,.03,.98,-0.08) 0s;
}

/* Paragraph text styles*/
.main-small-title{
    font-size: 1rem;
    color: black;
    margin-bottom: .5rem;
}

